/*
    @package diTheme
    ##################################################
    |   CONTACT PAGE STYLES                          |
    ##################################################
*/
@use "sass:math";
@import './modules/reset';
@import './modules/variables';
@import './modules/mixins';
@import './modules/formElements';
@import './modules/buttons';

.contact-main {
    min-height: calc(100vh - 60px);
    overflow-x: hidden;
    position: relative;
    width: 100%;
}

.bg {
    bottom: 0;
    left: -130%;
    overflow: hidden;
    position: absolute;
    top: 0;
    width: 420%;
    z-index: -1;

    svg {
        fill: $di__light;
        height: 100%;
        object-fit: fill;
        opacity: .05;
        overflow: hidden;
    }
}

.container {
    display: flex;
    justify-content: center;
}

.contact-links {
    align-items: center;
    display: flex;
    flex: 0;
    flex-direction: column;
    justify-content: flex-start;
    margin: 76px 20px 0 0;
    position: relative;
    width: 30px;

    a {
        display: block;
        margin-bottom: 15px;

        svg {
            fill: $di__light;
            height: 20px;
        }

        &:active {
            svg {
                fill: $di__primary;
            }
        }
        &:hover {
            svg {
                fill: $di__primary;
            }
        }
    }
}

.contact-form {
    flex: 1;
    margin-bottom: 40px;
}

h2 {
    color: $di__primary;
    flex: 1;
    font-size: 1.5rem;
    font-weight: 400;
    margin: 40px 0 5px 0;
    padding-left: 20px;
    text-transform: lowercase;
}

.contact-bar {
    background: $di__primary;
    height: 1px;
    margin-left: 20px;
    width: 125px;
}

#di-contact-form {
    border-left: solid 1px $di__primary;
    flex: 1;
    margin-top: 15px;
    padding-left: 20px;
    position: relative;
}

@media screen and (min-width: 320px) {
    .bg {
        bottom: 0;
        left: -100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        width: 420%;
        z-index: -1;
    
        svg {
            fill: $di__light;
            height: 100%;
            object-fit: fill;
            opacity: .15;
            overflow: hidden;
        }
    }

    .contact-links {
        margin-right: 20px;

        a {
            margin-bottom: 20px;
    
            svg {
                height: 25px;
            }
        }
    }
    .contact-bar {
        margin-left: 20px;
    }

    
    h2, #di-contact-form {
        padding-left: 20px;
    }
}

